form {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 30%;
    background-color: rgb(226, 226, 226);
}

textarea {
    margin: 10px auto;
    border-radius: 5px;
    height: 70px;
    width: 100%;
    resize: none;
    outline: none;
    border: 2px solid;
    
}
label {
    text-align: center;
    font-family: sans-serif;
    font-size: 1.4rem;
    line-height: 2.3rem;
    color: #292929;
}

.todo-form__button-container {
    padding: 5px;
    display: flex;
    justify-content: space-around;
}

.todo-form__button {
    background-color: #47a;
    color: #292929;
    border: none;
    padding: 10px;
    border-radius: 5px;
}
.todo-form__button:hover {
    background-color: #47a5;
    cursor: pointer;
}

.todo-form__button--add {
    background: #61DAFA;
    box-shadow: 0 5px 25px rgba(97,218,250,.5);
}

.todo-form__button--cancel {
    background: transparent;

}